#nav {
    li {
        a {
            color: #000;
            font-weight: 400;
            font-size: 2rem;
            padding: 0px 10px 0px 10px;
            &:hover {
                background-color: transparent;
            }
        }
    }
}

.letter {
    display: inline-block;
}

.announcement {
    display: none;
}

.brand-logo {
    margin-left: 5%;
}

a:active,
a:focus {
    outline: 0;
    border: none;
    -moz-outline-style: none;
}

@media only screen and (min-width: 993px) {
    .hide-on-large {
        display: none;
    }
}
